import React, { useContext, useEffect, useRef } from "react";

import { DispatchContext } from "./context/SettingsContext";

import ArrowLeft from "@material-ui/icons/ArrowLeft";
import GetApp from "@material-ui/icons/GetApp";

import Tooltip from "@material-ui/core/Tooltip";

import useStyles from "./styles/DrawerHeaderStyles";

import { videoCodecs } from "./consts";

function DrawerHeader({ toggleDrawerOpen }) {
    const classes = useStyles();
    const dispatch = useContext(DispatchContext);

    let supported = useRef(false);
    useEffect(() => {
        supported.current =
            videoCodecs.find(codec => MediaRecorder.isTypeSupported(codec))
                .length > 0;
    });

    const toggleCapture = () => {
        const action = {
            type: "toggle",
            key: "app",
            value: "capture",
        };
        dispatch(action);
    };
    return (
        <div className={classes.root}>
            <span>Settings</span>
            <div
                className={classes.closeDrawerButton}
                onClick={toggleDrawerOpen}
            >
                <ArrowLeft />
            </div>
            {supported.current && (
                <Tooltip title="Save animation as video" placement="bottom-end">
                    <div
                        className={classes.downloadButton}
                        onClick={toggleCapture}
                    >
                        <GetApp />
                    </div>
                </Tooltip>
            )}
        </div>
    );
}

export default DrawerHeader;
